import React, { useState, useEffect } from 'react';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Badge } from '@/components/ui/badge';
import { CreditCard, Plus, CheckCircle, AlertCircle } from 'lucide-react';
import { toast } from 'sonner';
import axiosInstance from '@/api/axios/axiosInstance';

const TestCreditsPage = () => {
  const [loading, setLoading] = useState(true);
  const [creditData, setCreditData] = useState(null);
  const [packages, setPackages] = useState([]);
  const [error, setError] = useState(null);

  useEffect(() => {
    fetchCreditData();
  }, []);

  const fetchCreditData = async () => {
    try {
      setLoading(true);
      setError(null);
      
      console.log('üîç Testing credit system endpoints...');
      
      // Test credit balance
      const balanceRes = await axiosInstance.get('/api/credits/balance');
      console.log('‚úÖ Credit balance:', balanceRes.data);
      setCreditData(balanceRes.data.data);
      
      // Test credit packages
      const packagesRes = await axiosInstance.get('/api/credits/packages');
      console.log('‚úÖ Credit packages:', packagesRes.data);
      setPackages(packagesRes.data.data || []);
      
      toast.success('Credit system loaded successfully!');
    } catch (error) {
      console.error('‚ùå Error fetching credit data:', error);
      setError(error.response?.data?.message || error.message);
      toast.error('Failed to load credit system');
    } finally {
      setLoading(false);
    }
  };

  const testPaymentIntent = async (packageId) => {
    try {
      console.log('üîç Testing payment intent creation...');
      const response = await axiosInstance.post('/api/credits/purchase', {
        packageId: packageId
      });
      console.log('‚úÖ Payment intent created:', response.data);
      toast.success('Payment intent created successfully!');
    } catch (error) {
      console.error('‚ùå Payment intent error:', error);
      toast.error('Payment intent failed: ' + (error.response?.data?.message || error.message));
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="text-center">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto mb-4"></div>
          <p>Loading credit system...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="container mx-auto p-6 space-y-6">
      <div className="flex items-center justify-between">
        <h1 className="text-3xl font-bold">Credit System Test</h1>
        <Button onClick={fetchCreditData} variant="outline">
          Refresh Data
        </Button>
      </div>

      {error && (
        <Card className="border-red-200 bg-red-50">
          <CardContent className="p-4">
            <div className="flex items-center space-x-2">
              <AlertCircle className="h-5 w-5 text-red-600" />
              <span className="text-red-800">Error: {error}</span>
            </div>
          </CardContent>
        </Card>
      )}

      {/* Credit Balance */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center space-x-2">
            <CreditCard className="h-5 w-5" />
            <span>Credit Balance</span>
          </CardTitle>
        </CardHeader>
        <CardContent>
          {creditData ? (
            <div className="space-y-2">
              <div className="flex items-center justify-between">
                <span>Available Credits:</span>
                <Badge variant={creditData.isSuperAdmin ? "default" : "secondary"}>
                  {creditData.available_credits}
                </Badge>
              </div>
              <div className="flex items-center justify-between">
                <span>Total Credits:</span>
                <span>{creditData.total_credits}</span>
              </div>
              <div className="flex items-center justify-between">
                <span>Used Credits:</span>
                <span>{creditData.used_credits}</span>
              </div>
              {creditData.isSuperAdmin && (
                <div className="flex items-center space-x-2 text-green-600">
                  <CheckCircle className="h-4 w-4" />
                  <span>Super Admin - Unlimited Credits</span>
                </div>
              )}
            </div>
          ) : (
            <p>No credit data available</p>
          )}
        </CardContent>
      </Card>

      {/* Credit Packages */}
      <Card>
        <CardHeader>
          <CardTitle className="flex items-center space-x-2">
            <Plus className="h-5 w-5" />
            <span>Available Credit Packages</span>
          </CardTitle>
        </CardHeader>
        <CardContent>
          {packages.length > 0 ? (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {packages.map((pkg) => (
                <Card key={pkg.id} className="border-2 hover:border-blue-300 transition-colors">
                  <CardContent className="p-4">
                    <div className="space-y-2">
                      <div className="flex items-center justify-between">
                        <h3 className="font-semibold">{pkg.name}</h3>
                        {pkg.is_popular && (
                          <Badge variant="default">Popular</Badge>
                        )}
                      </div>
                      <p className="text-sm text-gray-600">{pkg.description}</p>
                      <div className="space-y-1">
                        <div className="flex justify-between">
                          <span>Credits:</span>
                          <span>{pkg.total_credits}</span>
                        </div>
                        <div className="flex justify-between">
                          <span>Price:</span>
                          <span className="font-semibold">${pkg.price_dollars}</span>
                        </div>
                        {pkg.bonus_credits > 0 && (
                          <div className="flex justify-between text-green-600">
                            <span>Bonus:</span>
                            <span>+{pkg.bonus_credits} credits</span>
                          </div>
                        )}
                      </div>
                      <Button 
                        onClick={() => testPaymentIntent(pkg.package_id)}
                        className="w-full mt-2"
                        size="sm"
                      >
                        Test Purchase
                      </Button>
                    </div>
                  </CardContent>
                </Card>
              ))}
            </div>
          ) : (
            <p>No credit packages available</p>
          )}
        </CardContent>
      </Card>
    </div>
  );
};

export default TestCreditsPage;
